<template>
    <div class="seller" ref="seller">
        <div class="seller-content">
            <div class="overveiw">
                <h1 class="title">{{seller.name}}</h1>
                <div class="desc border-1px">
                    <star :size="36" :score="seller.score"></star>
                    <span class="text">({{seller.ratingCount}})</span>
                    <span class="text">月售{{seller.sellCount}}单</span>
                </div>
                <ul class="remark">
                    <li class="block">
                        <h2>起送价</h2>
                        <div class="content">
                            <span class="stress">{{seller.minPrice}}</span>元
                        </div>
                    </li>
                    <li class="block">
                        <h2>商家配送</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryPrice}}</span>元
                        </div>
                    </li>
                    <li class="block">
                        <h2>平均配送时间</h2>
                        <div class="content">
                            <span class="stress">{{seller.deliveryTime}}</span>分钟
                        </div>
                    </li>
                </ul>
                <div class="favorite" @click="toggleFavorite">
                    <span class="icon-favorite" :class="{'active':favorite}"></span>
                    <span class="text">{{favoriteText}}</span>
                </div>
            </div>
            <split></split>
            <div class="bulletin">
                <h1 class="title">
                    公告与活动
                </h1>
                <div class="content-wrapper border-1px">
                    <p class="content">{{seller.bulletin}}</p>
                </div>
                <!-- 优惠信息 -->
                <ul v-if="seller.supports" class="supports">
                    <li class="support-item" :key="item.index" v-for="(item,index) in seller.supports">
                        <i class="icon" :class="classMap[seller.supports[index].type]"></i>
                        <span class="text">{{seller.supports[index].description}}</span>
                    </li>
                </ul>
            </div>
            <split></split>
            <div class="pics">
                <h1 class="title">商家实景</h1>
                <div class="pic-wrapper" ref="picWrapper">
                    <ul class="pic-list" ref="picList">
                        <li class="pic-item" v-for="(pic,index) in seller.pics" :key="index">
                            <img :src="pic" width="120" height="90">
                        </li>
                    </ul>
                </div>
            </div>
            <split></split>
            <div class="info">
                <h1 class="title">商家信息</h1>
                <ul>
                    <li class="info-item" v-for="(info,index) in seller.infos" :key="index">{{info}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
import BScroll from "better-scroll";
import { saveToLocal,loadFromLocal } from "@/common/js/store";
import star from "@/components/star/star";
import split from "@/components/split/split";

    export default {
        props: {
            seller: {
                type: Object
            }
        },
        data () {
            return {
                favorite: (() => {
                    return loadFromLocal(this.seller.id, 'favorite', false);
                })()
            };
        },
        computed: {
            favoriteText() {
                return this.favorite ? '已收藏' : '收藏';
            }
        },
        methods: {
            _initScroll() {
                if(!this.scroll) {
                    this.scroll = new BScroll(this.$refs.seller, {
                        click: true
                    });
                }else{
                    this.scroll.refresh();
                }
            },
            _initPics() {
                if (this.seller.pics) { //有图片时候执行
                    let picWidth = 120;
                    let margin = 6;
                    let width = (picWidth + margin) * this.seller.pics.length - margin; //内层宽度超过wrapper宽度才能滚动
                    this.$refs.picList.style.width = width + 'px';
                    this.$nextTick(() => {
                        if (!this.picScroll) {
                            this.picScroll = new BScroll(this.$refs.picWrapper, {
                                scrollX: true, //横向滚动
                                eventPassthrough: 'vertical' //横向滚动忽略纵向滚动
                            });
                        } else {
                            this.picScroll.refresh();
                        }
                    });
                }
            },
            toggleFavorite(event) {
               if(!event._constructed) {
                   return;
               }
               this.favorite = !this.favorite;
               //localStorage.favorite = this.favorite; 直接直接本地存储，无法判断是哪个商家，所以封装函数
               saveToLocal(this.seller.id,'favorite',this.favorite);
            }
        },
        created() {
            this.classMap = ["decrease", "discount", "special", "invoice", "guarantee"];
        },
        watch: {
            'seller'() { //当seller数据变话时候
               this.$nextTick(() => {
                    this._initScroll();
                    this._initPics();
                }) 
            }
        },
        mounted() { //mounted比watch早运行，所以_initScroll中要加else判断，重新计算
            this.$nextTick(() => { //必须保证DOM被渲染完成才加载
                this._initScroll();
                this._initPics();
            })
        },
        components: {
            star,
            split
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scope>
@import '../../common/sass/mixins';
    .seller{
        position: absolute;
        top: 174px;
        bottom: 0;
        left: 0;
        width: 100%;
        overflow: hidden;
        .overveiw{
            padding: 18px;
            .title{
                margin-bottom: 8px;
                line-height: 14px;
                color: rgb(7, 17, 27);
                font-size: 14px;
            }
            .desc{
                padding-bottom: 18px;
                font-size: 0;
                @include border-1px(rgba(7,17,27,0.1));
                .star{
                    display: inline-block;
                    margin-right: 8px;
                    vertical-align: top;
                }
                .text{
                    margin-right: 12px;
                    display: inline-block;
                    line-height: 18px;
                    vertical-align: top;
                    font-size: 10px;
                    color: rgb(77, 85, 93);
                }
            }
            .remark{
                display: flex;
                padding-top: 18px;
                .block{
                    flex: 1;
                    text-align: center;
                    border-right: 1px solid rgba(7,17,27,0.1);
                    &:last-child{
                        border: none;
                    }
                    h2{
                        margin-bottom: 4px;
                        line-height: 10px;
                        font-size: 10px;
                        color: rgb(147, 153, 159);
                    }
                    .content{
                        line-height: 24px;
                        font-size: 10px;
                        color: rgb(7, 17, 27);
                        .stress{
                            font-size: 24px;
                        }
                    }
                }
            }
            .favorite{
                position: absolute;
                width: 50px;
                right: 11px;
                top: 18px;
                text-align: center;
                .icon-favorite{
                    display: block;
                    margin-bottom: 4px;
                    line-height: 24px;
                    font-size: 24px;
                    color: #d4d6d9;
                    &.active{
                        color: rgb(240, 20, 20);
                    }
                }
                .text{
                    line-height: 10px;
                    font-size: 10px;
                    color: rgb(77, 85, 93);
                }
            }
        }
        .bulletin{
            padding: 18px 18px 0 18px;
            .title{
                margin-bottom: 8px;
                line-height: 14px;
                color: rgb(7, 17, 27);
                font-size: 14px;
            }
            .content-wrapper{
                padding: 0 12px 16px 12px;
                @include border-1px(rgba(7,17,27,0.1));
                .content{
                    line-height: 24px;
                    font-size: 12px;
                    color: rgb(240, 20, 20)
                }
            }
            .supports{
                .support-item{
                    padding: 16px 12px;
                    @include border-1px(rgba(7,17,27,0.1));
                    font-size: 0;
                    &:last-child{
                        @include border-none();
                    }
                    .icon{
                        display: inline-block;
                        vertical-align: top;
                        width: 16px;
                        height: 16px;
                        margin-right: 6px;
                        background-size: 16px 16px;
                        background-repeat: no-repeat;
                        &.decrease {
                            @include bg-img(decrease_4);
                        }
                        &.discount {
                            @include bg-img(discount_4);
                        }
                        &.guarantee {
                            @include bg-img(guarantee_4);
                        }
                        &.invoice {
                            @include bg-img(invoice_4);
                        }
                        &.special {
                            @include bg-img(special_4);
                        }
                    }
                    .text{
                        line-height: 16px;
                        font-size: 12px;
                        color: rgb(7, 17, 27);
                    }
                }
            }
        }
        .pics{
            padding: 18px;
            .title{
                margin-bottom: 12px;
                line-height: 14px;
                color: rgb(7, 17, 27);
                font-size: 14px;
            }
            .pic-wrapper{
                width: 100%;
                overflow: hidden;
                white-space: nowrap; //不折行
                .pic-list{
                    font-size: 0;
                    .pic-item{
                        display: inline-block;
                        margin-right: 6px;
                        width: 120px;
                        height: 90px;
                        &:last-child{
                            margin: 0;
                        }
                    }
                }
            }
        }
        .info{
            padding: 18px 18px 0 18px;
            color: rgb(7, 17, 27);
            .title{
                padding-bottom: 12px;
                line-height: 14px;
                font-size: 14px;
                @include border-1px(rgba(7,17,27,0.1));
            }
            .info-item{
                padding: 16px 12px;
                line-height: 16px;
                @include border-1px(rgba(7,17,27,0.1));
                font-size: 12px;
                &:last-child{
                    @include border-none();
                }
            }
        }
    }
</style>



